@import url("https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap");
$text-color: #2b2d42;
$link-color: #2b2d42;
$link-hover-color: #e71d36;
$link-hover-filter: invert(33%) sepia(92%) saturate(7107%) hue-rotate(344deg) brightness(94%) contrast(93%);
$link-background-color: #eae0d5;
$background-color: #edf2f4;

//text style

@mixin link {
    font-size: 26px;
    color: $link-color;
    font-weight: 500;
    line-height: 40px;
    text-decoration: none;
}

* {
    margin: 0;
    padding: 0;
    font-family: Quicksand;
}
html,
body,
#root,
.App {
    height: 100%;
}
h1,h2,h3,h4 {
    margin: 0;
}
body {
    background: $background-color;
}

.App {
    position: relative;
    margin: auto;
    margin-block: 0;
}
.home {
    padding-top: 1rem;
}
a {
    @include link;
    margin: 0 auto;
    text-align: center;
}

.contact-list > div {
    a {
        width: 300px;
        border: 2px solid grey;
        border-radius: 5px;
        display: block;
        padding: 10px 5px;
        margin-bottom: 10px;
        text-transform: capitalize;
        transition: all 200ms;
    }
    a:hover,
    a:focus {
        color: $link-hover-color;
        border-color: $text-color;
        box-shadow: 0 0.5em 0.5em -0.5em $link-color;
        transform: translateY(-0.05em);
    }
    .link {
        display: flex;
        justify-content: center;
        align-items: center;
        position: relative;
        .icon,
        img {
            width: 32px;
            height: 32px;
            position: absolute;
            left: 5px;
        }
    }
}

.home {
    text-align: center;
    > * {
        padding-block: 0.5rem;
    }
}

.hi {
    font-size: 54px;
    font-weight: bold;
    
}
h1 {
    margin-bottom: 1rem;
}
u {
    text-decoration: underline $link-hover-color;
}

.contact-list {
    margin-bottom: 20px;
}
footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #e71d36;
    padding-block: 20px;
    text-align: center;
    background-color: #e4e9eb;
    font-size: 1.2rem;
}
